<style lang="less">
@import "../assets/less/content.less";
</style>
<template>
  <div id="main1">
    <div class="left">
      <h1 class="content-h1title">{{article.title}}</h1>
      <div class="content-gap">
        <span>{{article.createtime | formatTime}}</span>
      </div>
      <div class="content-content" v-html="article.content"></div>
      <div class="article-reco">
        <h2>相关推荐</h2>
        <ul>
          <li v-for="(a,index) in articleReco">
            <em></em>
            <router-link :to="a.link">{{a.title}}</router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="right">
      <part-right :topnode="article.nodeid" :nodename="article.nodename"></part-right>
    </div>
    <div style="clear:both;"></div>
  </div>
</template>

<script>
import partRight from "../components/part-right/index";
export default {
  data() {
    return {
      article: {},
      crumbParam: {},
      articleReco: []
    };
  },
  components: {
    "part-right": partRight
  },
  methods: {
    async getArticleInfo() {
      const param = this.$route.params;
      const content = await this.$tools.httpAgent(
        "/api/article/get",
        "post",
        "id=" + param.id
      );
      this.article = content.data;
      this.getArticles(content.data.nodeid);
      // this.crumbParam = {
      //   nodeid: this.article.nodeid
      // };
      document.title = this.article.title + "-" + document.title;
      this.$tools.setMetaKey(this.article.title + "-" + document.title);
      this.$tools.setMetaDes(this.article.title + "-" + document.title);
    },
    async getArticles(nodeid) {
      const article = await this.$tools.httpAgent("/api/article/page", "post", {
        nodeid,
        cp: 1,
        mp: 10,
        istp: false
      });
      this.articleReco = article.data.data;
    }
  },
  computed: {},
  async mounted() {
    this.getArticleInfo();
  },
  watch: {
    $route() {
      this.getArticleInfo();
    }
  }
};
</script>
